import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

let routers = new Router({
  routes: [
    // 首页
    {
      path: "/",
      name: "home",
      component: () => import("./views/Home.vue"),
    },
    // 关于我们
    {
      path: "/companyProfile",
      name: "companyProfile",
      component: () => import("./views/companyProfile.vue"),
    },
    // 服务内容
    {
      path: "/businessLine",
      name: "businessLine",
      component: () => import("./views/businessLine.vue"),
    },
    // 客户案例
    {
      path: "/cooperationCases",
      name: "cooperationCases",
      component: () => import("./views/cooperationCases.vue"),
    },
    // 行业资讯
    {
      path: "/honorQualification",
      name: "honorQualification",
      component: () => import("./views/honorQualification.vue"),
    },
    // 联系我们
    {
      path: "/contactUs",
      name: "contactUs",
      component: () => import("./views/contactUs.vue"),
    },
  ],
});

export default routers;
