<template>
  <div id="app">
    <el-container>
      <!-- 头部 -->
      <el-header class="animate__animated animate__fadeIn">
        <div class="logo">
          <img v-lazy="require('./assets/img/top_logo.png')" class="logo_img" />
        </div>
        <el-menu
          :default-active="defaultActive"
          mode="horizontal"
          :router="router"
          text-color="#141414"
          active-text-color="#16A5A6"
        >
          <el-menu-item
            v-for="(item, index) in pageList"
            :key="index"
            :index="item.index"
          >
            {{ item.name }}
          </el-menu-item>
        </el-menu>
        <div class="header_phone">
          <img
            v-lazy="require('./assets/img/header_phone.png')"
            class="header_phone_icon"
          />
          <span class="header_phone_title">咨询热线</span>
          <span class="header_phone_txt">
            13051525673
          </span>
        </div>
      </el-header>
      <el-main style="min-height: 1000px;">
        <router-view />
      </el-main>
      <!-- 底部 -->
      <div class="footer">
        <div class="footer_img">
          <img v-lazy="require('./assets/img/message_right_img_1.png')" />
          <img
            style="margin-left: 16px;"
            v-lazy="require('./assets/img/message_right_img_2.png')"
          />
        </div>
        <div class="footer_message">
          <div class="footer_message_txt">
            公司名称：北京人人通信息咨询有限公司
          </div>
          <div class="footer_message_txt">
            公司地址：北京市门头沟区新桥大街2号2幢5层518/河北省三河市燕郊东方夏威夷Y栋4单元2门472
          </div>
          <div class="footer_message_txt">
            联系电话：13051525673/13522408320
          </div>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      router: true,
      defaultActive: "/",
      pageList: [
        { name: "首页", index: "/" },
        { name: "关于我们", index: "/companyProfile" },
        { name: "服务内容", index: "/businessLine" },
        { name: "客户案例", index: "/cooperationCases" },
        { name: "行业资讯", index: "/honorQualification" },
        { name: "联系我们", index: "/contactUs" },
      ],
      show: false,
    };
  },
  methods: {
    changeShow() {
      this.show = !this.show;
    },
  },
  created() {},
  watch: {
    $route(to) {
      this.defaultActive = to.path;
    },
  },
  beforeCreate() {
    /* eslint-disable */
    let count = 0;
    let imgs = [
      //用require的方式添加图片地址，直接添加图片地址的话，在build打包之后会查找不到图片，因为打包之后的图片名称会有一个加密的字符串
      require("./assets/resource/关于我们.jpg"),
      require("./assets/resource/专注服务.jpg"),
      require("./assets/resource/合作案例.jpg"),
      require("./assets/resource/行业资讯.jpg"),
      require("./assets/resource/联系我们.jpg"),
    ];
    for (let img of imgs) {
      let image = new Image();
      image.onload = () => {
        count++;
      };
      image.src = img;
    }
  },
};
</script>

<style lang="scss">
* {
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.el-header {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-content: center;
  margin: 30px auto;
  padding: 0 !important;
  .el-menu.el-menu--horizontal {
    border-bottom: solid 0px #e6e6e6;
  }
  .logo {
    height: 60px;
    display: flex;
    align-items: center;

    .logo_img {
      width: 176px;
      height: 48px;
      object-fit: contain;
    }
  }

  .header_phone {
    line-height: 60px;
    font-size: 20px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    .header_phone_icon {
      width: 24px;
      height: 24px;
      color: #333333;
      margin-right: 2px;
    }
    .header_phone_title {
      font-weight: 400;
      font-size: 18px;
      color: #000000;
      margin-right: 12px;
    }
    .header_phone_txt {
      font-weight: bold;
      font-size: 20px;
      color: #16a5a6;
    }
  }
}

.el-main {
  padding: 0 !important;
}

.footer {
  height: 160px;
  width: 100%;
  background: #000000;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  .footer_message {
    height: 90px;
    font-size: 18px;
    margin-left: 40px;
    .footer_message_txt {
      font-weight: 400;
      height: 30px;
      line-height: 30px;
      font-size: 16px;
      color: #ffffff;
    }
  }
  .footer_img {
    img {
      width: 90px;
      height: 90px;
      object-fit: contain;
    }
  }
}

// 全局滚动条
::-webkit-scrollbar {
  width: 6px;
  background: rgba($color: #000000, $alpha: 0);
  cursor: pointer;
}

// 滚动条里面小方块
::-webkit-scrollbar-thumb {
  height: 4px;
  width: 4px;
  background: $subject_color;
  border-radius: 4px;
}

// 滚动条里面轨道
::-webkit-scrollbar-track {
  height: 4px;
  width: 4px;
  background: rgba($color: #000000, $alpha: 0);
  border-radius: 4px;
}
</style>
