function baseurl() {
	let env = process.env.NODE_ENV
	let envobj = {
		development: process.env.VUE_APP_URL_DEVELOPMENT,
		production: process.env.VUE_APP_URL_PRODUCTION,
	}
	return envobj[env]
}
const baseURL = baseurl()
module.exports = baseURL
