const getDefaultState = () => {
	return {
		// 首次打开进入首页令牌
		firstOpen: sessionStorage.getItem('firstOpen')
			? JSON.parse(sessionStorage.getItem('firstOpen'))
			: null,
		// 用户信息
		userInfo: sessionStorage.getItem('userInfo')
			? JSON.parse(sessionStorage.getItem('userInfo'))
			: {},
	}
}

const state = getDefaultState()

const mutations = {
	SET_TOKEN: (state, firstOpen) => {
		state.firstOpen = firstOpen
	},
	SET_USER_INFO: (state, userInfo) => {
		state.userInfo = userInfo
	},
}

const actions = {
	setfirstOpen({ commit }, firstOpen) {
		commit('SET_TOKEN', firstOpen)
		sessionStorage.setItem('firstOpen', JSON.stringify(firstOpen))
	},
	setUserInfo({ commit }, userInfo) {
		commit('SET_USER_INFO', userInfo)
		sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
	},
	logOut() {
		state.firstOpen = ''
		sessionStorage.clear()
		location.reload()
	},
}

export default {
	state,
	mutations,
	actions,
	namespaced: true,
}
