import CryptoJS from 'crypto-js'

/**
 * AES 加密
 * @param {*} src  明文
 * @param {*} keyWord  密钥
 * @returns 密文
 */
export function aesEncrypt(src, keyWord = 'zhongrunsafezrjs') {
	// 密钥
	const key = CryptoJS.enc.Utf8.parse(keyWord)
	// 偏移量
	var iv = key
	// 加密
	var encrypted = CryptoJS.AES.encrypt(src, key, {
		iv: iv,
		mode: CryptoJS.mode.CBC,
		padding: CryptoJS.pad.ZeroPadding,
	})
	return encrypted.toString()
}

/**
 *  解密
 * @param {*} result 密文
 * @param {*} keyWord 密钥
 * @returns 明文
 */
export function aesDecrypt(result, keyWord = 'zhongrunsafezrjs') {
	// 密钥
	const key = CryptoJS.enc.Latin1.parse(keyWord)
	// 偏移量
	var iv = key
	// 解密逻辑
	var decryptd = CryptoJS.AES.decrypt(result, key, {
		iv: iv,
		mode: CryptoJS.mode.CBC,
		padding: CryptoJS.pad.ZeroPadding,
	})
	return decryptd.toString(CryptoJS.enc.Utf8)
}
