import VueEventBus from "./eventBus";
import ElementUI from "element-ui";
import HomeTitle from "@/components/HomeTitle";
import TopTitle from "@/components/TopTitle";
import Article from "@/components/Article";
import NoData from "@/components/NoData";
import SvgIcon from "@/components/SvgIcon";
import GeneralTitle from "@/components/GeneralTitle";

import animate from "animate.css";
import VueLazyload from "vue-lazyload";
import VideoPlayer from "vue-video-player";

import { getLabel, tableDataPick, listToTree, deepClone } from "@/lib/util";
import { aesEncrypt, aesDecrypt } from "@/lib/cryptojs";

import "element-ui/lib/theme-chalk/index.css";
import "video.js/dist/video-js.css";
import "vue-video-player/src/custom-theme.css";
import "@/style/index.scss";

const baseURL = require("../../base.config.js");

export default (Vue) => {
  Vue.component("HomeTitle", HomeTitle);
  Vue.component("TopTitle", TopTitle);
  Vue.component("Article", Article);
  Vue.component("NoData", NoData);
  Vue.component("SvgIcon", SvgIcon);
  Vue.component("GeneralTitle", GeneralTitle);

  Vue.prototype.$baseURL = baseURL;
  Vue.prototype.$getLabel = getLabel; // list翻译方法
  Vue.prototype.$tableDataPick = tableDataPick; // table列隐显控制方法
  Vue.prototype.$listToTree = listToTree; // list转tree方法
  Vue.prototype.$clone = deepClone; // 深拷贝方法
  Vue.prototype.$aesEncrypt = aesEncrypt; // 加密
  Vue.prototype.$aesDecrypt = aesDecrypt; // 解密

  Vue.use(VueEventBus);
  Vue.use(ElementUI);
  Vue.use(VideoPlayer);
  Vue.use(animate);

  Vue.use(VueLazyload, {
    //完全显示的时候加载
    preLoad: 1,
    //失败时显示的图片
    error: require("@/assets/img/error.png"),
    //加载时显示的GIF图
    loading: require("@/assets/img/loading.gif"),
    //尝试加载几次
    attempt: 1,
  });

  const req = require.context("../assets/svg", false, /\.svg$/);
  const requireAll = (requireContext) =>
    requireContext.keys().map(requireContext);
  requireAll(req);
};
