 * @Description: 顶部公共标题图片部分
-->
<template>
  <div
    class="top_title"
    :style="{
      backgroundImage:
        'url(' + require(`../assets/resource/${subheading + '.jpg'}`) + ') ',
    }"
  >
    <div :class="[getColorBg()]">
      <div
        :class="isBlack ? 'top_title_subheading_black' : 'top_title_subheading'"
      >
        {{ subheading }}
      </div>
      <div :class="isBlack ? 'top_title_big_black' : 'top_title_big'">
        {{ title }}
      </div>
      <div :class="isBlack ? 'top_title_content_black' : 'top_title_content'">
        {{ content }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "Company Profile",
    },
    subheading: {
      type: String,
      default: "关于我们",
    },
    content: {
      type: String,
      default: "安全风险管理一站式专业服务机构",
    },
    isBlack: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getColorBg() {
      let str = "";
      switch (this.subheading) {
        case "专注服务":
          str = "top_title_parallelogram_blue";
          break;
        case "关于我们":
          str = "top_title_parallelogram_green";
          break;
        case "合作案例":
          str = "top_title_parallelogram_black";
          break;
        case "联系我们":
          str = "top_title_parallelogram_cambridge";
          break;
        case "行业资讯":
          str = "top_title_parallelogram_blue";
          break;
        default:
          str = "top_title_parallelogram_black";
          break;
      }
      return str;
    },
  },
};
</script>

<style lang="scss" scoped>
.top_title {
  width: 100%;
  height: 500px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  .top_title_subheading {
    min-width: 200px;
    margin: 0 auto;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 0em;
    color: #ffffff;
    padding-top: 190px;
    font-weight: 900;
  }
  .top_title_big {
    min-width: 200px;
    margin: 0 auto;
    font-size: 25px;
    font-weight: normal;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0em;
    color: #ffffff;
    margin-top: 20px;
  }
  .top_title_content {
    min-width: 200px;
    margin: 0 auto;
    font-size: 25px;
    font-weight: normal;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0em;
    color: #ffffff;
    margin-top: 30px;
  }
  .top_title_subheading_black {
    min-width: 200px;
    margin: 0 auto;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 0em;
    padding-top: 190px;
    font-weight: 900;
    color: #333333;
  }
  .top_title_big_black {
    color: #7f7f7f;
    min-width: 200px;
    margin: 0 auto;
    font-size: 25px;
    font-weight: normal;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0em;
    margin-top: 20px;
  }
  .top_title_content_black {
    min-width: 200px;
    margin: 0 auto;
    font-size: 25px;
    font-weight: normal;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0em;
    color: #333333;
    margin-top: 30px;
  }
  .top_title_parallelogram_green {
    position: relative;
    width: 800px;
    height: 500px;
    margin: auto;
    z-index: 2;
  }
  .top_title_parallelogram_green::before {
    content: "";
    position: absolute;
    width: 800px;
    height: 500px;
    top: 0;
    left: 0;
    background-color: rgba(21, 165, 165, 0.6);
    transform: skewX(-20deg);
    z-index: -1;
  }
  .top_title_parallelogram_blue {
    position: relative;
    width: 800px;
    height: 500px;
    margin: auto;
    z-index: 2;
  }
  .top_title_parallelogram_blue::before {
    content: "";
    position: absolute;
    width: 800px;
    height: 500px;
    top: 0;
    left: 0;
    background-color: rgba(2, 39, 95, 0.6);
    transform: skewX(-20deg);
    z-index: -1;
  }
  .top_title_parallelogram_black {
    position: relative;
    width: 800px;
    height: 500px;
    margin: auto;
    z-index: 2;
  }
  .top_title_parallelogram_black::before {
    content: "";
    position: absolute;
    width: 800px;
    height: 500px;
    top: 0;
    left: 0;
    background-color: rgba(31, 31, 31, 0.6);
    transform: skewX(-20deg);
    z-index: -1;
  }
  .top_title_parallelogram_cambridge {
    position: relative;
    width: 800px;
    height: 500px;
    margin: auto;
    z-index: 2;
  }
  .top_title_parallelogram_cambridge::before {
    content: "";
    position: absolute;
    width: 800px;
    height: 500px;
    top: 0;
    left: 0;
    background-color: rgba(129, 190, 235, 0.6);
    transform: skewX(-20deg);
    z-index: -1;
  }
}
</style>
