<template>
  <div class="article_box_big">
    <div class="article_navigation">
      <span class="article_navigation_1" @click="articleBackFunc">{{
        goBackPage
      }}</span>
      <i class="el-icon-arrow-right"></i>
      <span class="article_navigation_2">{{ articleData.titleName }}</span>
    </div>
    <div class="article_box">
      <div class="article_top">
        <div class="article_title">
          {{ articleData.titleName }}
        </div>
        <div class="article_message">
          <span class="article_time" v-if="articleData.publishTime != ''">
            发布时间：
            {{ articleData.publishTime }}
          </span>
          <span class="article_author">
            作者：人人通
          </span>
        </div>
      </div>
      <div class="article_bottom">
        <div class="article_paragraph">
          <p
            v-for="(item, index) in processingData(articleData.content)"
            :key="index"
          >
            {{ item }}
          </p>
        </div>
        <div
          class="article_img"
          v-for="(item, index) in articleImgList"
          :key="index"
        >
          <img :src="$baseURL + item.fileUrl" width="100%" />
        </div>
      </div>
    </div>
    <div class="article_pagination_big">
      <div class="article_pagination">
        <div class="article_left">
          <!-- <div class="article_left_top">上一篇：</div>
					<div class="article_left_title">
						包头市宏源矿业有限责任公司尾矿库安全现状评价报告
					</div>
					<div class="article_left_icon">
						<i class="el-icon-back"></i>
					</div> -->
        </div>
        <div class="article_back">
          <div class="article_back_icon" @click="articleBackFunc"></div>
          <div class="article_back_txt">返回列表</div>
        </div>
        <div class="article_right">
          <!-- <div class="article_right_top">下一篇：</div>
					<div class="article_right_title">
						包头市宏源矿业有限责任公司尾矿库安全现状评价报告
					</div>
					<div class="article_right_icon">
						<i class="el-icon-right"></i>
					</div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    articleData: {
      type: Object,
      default() {
        return {};
      },
    },
    articleImgList: {
      type: Array,
      default() {
        return [];
      },
    },
    goBackPage: {
      type: String,
      default: "首页",
    },
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  computed: {},
  mounted() {},
  methods: {
    articleBackFunc() {
      this.$emit("articleBackFunc");
    },
    processingData(item) {
      let str = item.split("\n");
      return str;
    },
  },
};
</script>

<style lang="scss" scoped>
.article_box_big {
  background: #fafafa;
  width: 100%;
  min-height: 800px;
  padding-top: 30px;
}
.article_navigation {
  width: 1390px;
  padding: 20px 0;
  margin: 0 auto;
  i {
    padding: 0 4px;
    color: #c0c4cc;
  }
  .article_navigation_1 {
    font-weight: 400;
    cursor: pointer;
  }
  .article_navigation_1:hover {
    font-weight: 900;
    cursor: pointer;
  }
  .article_navigation_2 {
    font-weight: 900;
  }
}
.article_box {
  width: 1300px;
  min-height: 620px;
  padding: 50px;
  margin: 0 auto 50px;
  background: #fff;
  border-radius: 8px;
  .article_top {
    .article_title {
      font-size: 30px;
      font-weight: 500;
      line-height: 40px;
      color: #333333;
      text-align: center;
    }
    .article_message {
      width: 1000px;
      margin: 20px auto 30px;
      display: flex;
      justify-content: center;
      .article_time {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #999999;
        margin: 0 10px;
      }
      .article_author {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #999999;
        margin: 0 10px;
      }
    }
  }

  .article_bottom {
    width: 100%;
    border-top: 1px solid #ebebeb;
    padding-top: 30px;
    .article_paragraph {
      font-size: 14px;
      font-weight: normal;
      line-height: 30px;
      color: #333333;
      padding: 0 30px;
      p {
        text-indent: 30px;
      }
    }
    .article_img {
      max-width: 50%;
      margin: 50px auto 50px;
    }
  }
}
.article_pagination_big {
  width: 100%;
  height: 180px;
  background: #fff;
  .article_pagination {
    width: 1400px;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
    .article_left {
      .article_left_top {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #999999;
        margin-top: 50px;
        text-align: start;
      }
      .article_left_title {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #666666;
        margin-top: 10px;
        text-align: start;
      }
      .article_left_icon {
        margin-top: 10px;
        text-align: start;
      }
    }
    .article_back {
      .article_back_icon {
        width: 48px;
        height: 48px;
        background: url("../assets/img/article_back_icon.png") no-repeat;
        background-size: 100% 100%;
        margin: 50px auto 0;
      }
      .article_back_txt {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #999999;
        margin-top: 10px;
        text-align: center;
      }
    }
    .article_right {
      .article_right_top {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #999999;
        margin-top: 50px;
        text-align: end;
      }
      .article_right_title {
        font-size: 14px;
        font-weight: normal;
        line-height: 20px;
        color: #666666;
        margin-top: 10px;
        text-align: end;
      }
      .article_right_icon {
        margin-top: 10px;
        text-align: end;
      }
    }
  }
}
</style>
